// buttons
.form-container {
  text-align: center;
  font-size: 1rem;
  .form-button {
  }
  width: 100%;
}
.form-input, .form-button {
  display: block;
  border: 1px solid lightblue;
  color: lightblue;
  margin: 10px auto;
  width: 290px;
  height: 40px;
  line-height:  40px;
  font-size: 0.8rem;
}
.form-input {
  background: white;
  text-indent: 10px;
  &:focus {
    outline: none;
  }
  &.invalid {
    border: 1px solid red;
  }
  &.animateInvalid {
    animation: invalidInput .7s;
  }
}

.form-button {
  cursor: pointer;
  background: lightslategray;
  user-select: none;
  color:white;
  transition: background-color 0.5s ease;
  &.touchHover {
    background: lightblue;
  }


}


@keyframes invalidInput {
  0% {transform: translate(8px, 0);}
  10% {transform: translate(-8px, 0);}
  30% {transform: translate(8px, 0);}
  60% {transform: translate(-8px, 0);}
  100% {transform: translate(0, 0);}
}



